import { Button, MenuItem, Stack, TextField, Typography, useMediaQuery } from "@mui/material";
import { Form, Formik } from "formik";
import { useRef } from "react";
import { useDispatch, useSelector } from "react-redux";
import * as yup from 'yup';
import { appapi } from "../../../../../api/apiClient";
import FormTextField from "../../../../../components/FormTextField";
import { setLoading } from "../../../../../redux/features/appSlice";
import { initUserData } from "../../../../../redux/features/authSlice";

import { colors } from "../../../../../utils/colors";
import { buttonStyle2 } from "../../../../../utils/commonStyle";
import { showmessage } from "../../../../../utils/toastr";

/**
 * Validation Schema 
 */
 const validationSchema = yup.object({
    email: yup
        .string('Enter your Email')
        .matches(/^[\w-\.]+@([\w-]+\.)+[\w-]{2,4}$/, 'Email is Invalid')
        .required('Email is Required'),
    name: yup
        .string('Enter your Name')
        .min(3, 'Name must minimum of 3 characters')
        .max(100, 'Name must maximum of 100 characters')
        .required('Name is Required')
        .trim(),
    designation: yup
        .string('Enter your Department')
        .required('Designation is Required')
        .max(50, 'Name must maximum of 50 characters')
        .trim(),
    department: yup
        .string('Enter your Department')
        .required('Deparment is Required')
        .max(50, 'Name must maximum of 50 characters')
        .trim(),
    cityId: yup
        .string('Enter your Location')
        .required('Location is Required')
        .trim()
});

export default function UserProfile() {

    const isMobile = useMediaQuery("(max-width:600px)");

    const containerSX = isMobile ? { mt: '19px'} : {width: '718px', mt: '99px'};

    const textFieldDirection = isMobile ? 'column' : 'row';

    const formRef = useRef();

    const dispatch = useDispatch();

    const { user } = useSelector((state) => state.auth);

    const { citiesList } = useSelector(state => state.app);

    const updateProfile = async(values) => {
        dispatch(setLoading(true));
        let payload = {
            ...values,
            rolePermissionId: user.roleId
        };
        delete payload.email;
        const response = await appapi.put(`/user/${user.enterpriseId}/${user.enterpriseUserId}`, payload)
        if (response.ok) {
            const data = response.data
            if (data.status === 'success') {
                dispatch(initUserData())
            } else {
                showmessage(data.message, 2000, 'error')
            }
            dispatch(setLoading(false))
        } else {
            dispatch(setLoading(false))
            showmessage(`Something Went Wrong!`, 2000, 'error')
        }
    };

    return (
        <Stack>
            <Typography className="ff-Roboto fw-bold" sx={{color: colors.primaryHover}}>USER PROFILE</Typography>
            <Stack alignSelf={isMobile ? "unset":"center"}   sx={containerSX}>
                <Formik
                innerRef={formRef}
                enableReinitialize={true}
                initialValues={{
                    email: user?.emailId || '',
                    name: user?.name || '',
                    designation: user?.designation || '',
                    department: user?.department || '',
                    cityId: user?.cityId
                }}
                validationSchema={validationSchema}
                onSubmit={(values) => updateProfile(values)}
                >
                {({ handleSubmit, values, handleChange, errors, touched, handleBlur, isValid, dirty }) => (
                    <Form noValidate autoComplete="off">
                        <Stack sx={{ mb: "22px" }} direction={textFieldDirection} spacing={isMobile ? 0 : "62px"}>
                            <Stack flex={1}>
                                 <FormTextField label="Email" name="email" sx={{mb: 0}} disabled={values.email ? true : false} />
                            </Stack>
                           
                            <Stack flex={1}>
                                <FormTextField label="Name" name="name" sx={{mb: 0}}  />
                            </Stack>
                        </Stack>
                        <Stack sx={{ mb: "22px" }} direction={textFieldDirection} spacing={isMobile ? 0 : "62px"}>
                            <Stack flex={1}>
                                <FormTextField label="Designation" name="designation" sx={{mb: 0}}  />
                            </Stack>
                            <Stack flex={1}>
                               <FormTextField label="Department" name="department" sx={{mb: 0}}  />
                            </Stack>
                        </Stack>
                        <Stack sx={{ mb: "22px" }} direction={textFieldDirection} spacing={isMobile ? 0 : "62px"}>
                            <Stack flex={1}>
                                <Typography
                                style={{ color: colors.blackMain }}
                                className="fs-14 ff-Roboto fw-medium mb-7"
                                >
                                Location
                                </Typography>
                                <TextField
                                    fullWidth
                                    id='cityId'
                                    name='cityId'
                                    value={values.cityId || ''}
                                    onChange={handleChange}
                                    onBlur={handleBlur}
                                    select
                                // error={touched.cityId && Boolean(errors.cityId)}
                                >
                                    {citiesList.map((item) => (
                                        <MenuItem fullWidth key={item.cityId} value={item.cityId}> {item.cityName} </MenuItem>
                                    ))}
                                </TextField>
                            </Stack>
                            <Stack flex={1}></Stack>
                        </Stack>
                        <Button sx={{...buttonStyle2, width: isMobile ? '100%' : '328px'}} disabled={!(dirty && isValid)} type='submit'>SAVE</Button>
                    </Form>
                )}
                </Formik>
             </Stack>
        </Stack>
    );
}